<template lang="">
    <div class="getAccount d-flex align-items-center justify-content-between">
       <div class="form-input">
           <input v-model="code.token" type="text" class="form-control "  
           :class="{form_control__error: isError == true}" placeholder="Nhập Token">
            <div class="form-check m-2">
                <input class="form-check-input" :class="{form_error: isError == true}" 
                type="checkbox" id="checkbox" v-model="isCheck">
                <label for="checkbox"
                :class="{label_error: isError == true}">Đã đọc hướng dẫn</label>
            </div>
            <div class="d-flex">
                <button @click="getAccount" type="button" 
                class="btn btn-getAccount mt-1 d-block mx-auto"
                :disabled="isDisabled1"
                >Nhận tài khoản</button>
            <!-- <button v-if="isShow" @click="getAccount" type="button" class="btn btn-getAccount mt-3 d-block mx-auto">Bảo hành</button> -->
            </div>
            <div class="my-2">
                <a class="text-decoration-none" style="color: #FFC947" target="_blank" href="https://www.facebook.com/groups/585418816163609">
                   Tham gia cộng đồng để được phục vụ tốt hơn tại đây
                </a>
            </div>
            <div v-if="isShow" class="form account ">
                <div class="exit-icon">
                    <div @click="exitForm">
                        <svg  class="bi bi-x-lg float-end"   xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" viewBox="0 0 16 16">
                            <path d="M1.293 1.293a1 1 0 0 1 1.414 0L8 6.586l5.293-5.293a1 1 0 1 1 1.414 1.414L9.414 8l5.293 5.293a1 1 0 0 1-1.414 1.414L8 9.414l-5.293 5.293a1 1 0 0 1-1.414-1.414L6.586 8 1.293 2.707a1 1 0 0 1 0-1.414z"/>
                        </svg>
                    </div>
                </div>
                <div class="account-main px-5 mb-3">
                    <h5 style="font-weight: bold;">
                        Gói Netflix
                            <span v-if="client.qualify == 0">Standard</span>
                            <span v-if="client.qualify == 1">Premium</span>
                        : <span v-if="client.time == 0.5"> 15 ngày</span> 
                        <span v-else> {{ client.time }} tháng </span> 
                        
                    </h5>
                    <p>
                        Tài khoản: 
                        <span style="font-size: 20px">{{ client.username }}</span >
                    </p>
                    <p>
                        Mật khẩu:  <span  style="font-size: 20px">{{ client.password }}</span >
                    </p>
                        <button @click="getNewAccount" type="button" 
                        class="btn btn-getAccount mt-3 d-block mx-auto"
                        :disabled="isDisabled2"
                        >Bảo hành</button>
                </div>
           </div>      
       </div>
       <div class="getAccount-image">
           <Manual/>
       </div>
    </div>
</template>
<script>
import { mapState } from 'vuex'
import Manual from '../Manual.vue'
export default {
    components: {
        Manual
    },
    data() {
        return {
            code: {
                token: ''
            },
            isShow: false,
            isCheck: false,
            isError: false,
            isDisabled1: false,
            isDisabled2: false,
            isExit: false,
        }
    },
    computed:{ ...mapState(['client', 'errorReq'])},
    methods: {
        getAccount(){
            this.$store.commit('REFRESH_ERROR');
            if(this.isCheck == false || this.code.token == ''){
                this.$store.commit('HAVING_ERROR');
               return;
            }
            const promis = new Promise((resolve) => {    
                resolve(this.$store.dispatch('getAccount', this.code));
            });
            promis.then(()=> {
                if(this.errorReq == true){
                     this.isShow = false;
                     return;
                }
                else
                    this.isShow = true;
            });
          
            this.isDisabled1 = true;
            this.isError = false;         
            this.isCheck = false;
            setTimeout(() => {
                this.isDisabled1 = false;
                this.isExit = true;
            }, 2000);
        },
        getNewAccount(){
            this.isDisabled2 = true;
            this.$store.dispatch('getNewAccount', this.code);
            setTimeout(() => this.isDisabled2 = false, 2000);
        },
        exitForm() {
            this.isShow =! this.isShow;
            this.$store.commit('REFRESH_ERROR');
        }
    },
}
</script>
<style lang="css" scoped>
.getAccount {
    height: 86vh;
}
.getAccount .form-control {
    border-radius: 10px;
    padding-left: 30px;
    height: 50px;
    border: 2px solid #0A1931;
    box-shadow: 2px 2px #0A1931;
    transition: ease all .2s;
}
.getAccount .form-control:focus {
    border: 2px solid #0A1931;
    box-shadow: 1px 1px #0A1931;
    transform: translate(1px, 1px);
}
.getAccount .btn-getAccount {
    background: #FFC947;
    font-weight: bold;
    padding: 10px 30px;
    border-radius: 25px;
    border: 2px solid #0A1931;
    box-shadow: 2px 2px #0A1931;
    transition: ease all .2s;
}
.getAccount .btn-getAccount:hover {
    border: 2px solid #0A1931;
    box-shadow: 1px 1px #0A1931;
    transform: translate(2px, 2px);
    
}
.getAccount-image img {
    width: 100%;
    height: 100%;
}
.getAccount .form {
    background-color: rgba(255, 255, 255, 0.25);
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(20px);
    border-radius: 12px;
    border: 2px solid #000;
    box-shadow: 2px 2px #000;
    position: fixed;
    z-index: 999;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}
.getAccount .form span {
    font-weight: bold;
}
.getAccount .form-check label {
    color: #fff;
}
/*  */
.getAccount .form-input {
    width: 40%;
}
.getAccount .getAccount-image {
    width: 56%;
}
.exit-icon {
    height: 30px;
    position: relative;
}
.exit-icon svg {
    position: absolute;
    top: 10px;
    right: 20px;
    z-index: 999;
    cursor: pointer;
}
/*  */

.label_error {
    color: #DD067B!important; 
}
.form_error {
    background-color: 1px solid #DD067B !important;
}
.form_control__error {
    border: 2px solid rgb(212, 0, 0) !important;
    box-shadow: 2px 2px rgb(204, 0, 0) !important;
}

/*  */
@media only screen and (max-width: 1024px) {
    .getAccount {
        flex-direction: column-reverse;
        height: 100%;
    }
     .getAccount .getAccount-image {
        width: 100%;
        padding: 15px;
    }
    .getAccount .form-input {
        width: 70%;
    }
}
@media only screen and (max-width: 600px) {
    .getAccount {
        flex-direction: column-reverse;
        height: 100%;
    }
    .getAccount .form-control {
        padding-left: 10px;
    }
    .getAccount .getAccount-image {
        width: 100%;
        padding: 15px;
    }
    .getAccount .form-input {
        width: 100%;
        padding: 15px;
    }
    .getAccount .btn-getAccount {
        width: 100%;
    }
    .getAccount .form {
        border-radius: 10px;
        border: 1px solid #000;
        box-shadow: 2px 2px #000;
        width: 95%;
        margin: 0 auto;
    }
    .account-main {
        padding: 10px 15px !important;
    }
    .account-main h5 {
        font-size: 20px;
    }
}
</style>