<template lang="">
    <div class="manager">
        <ManagerEdit @popDown="isShow =! isShow" :isShow="isShow" :indexToken="indexToken"/>
        <div class="manager-navbar mx-auto d-flex justify-content-between w-50">
            <div class="manager-status">
                <span class="status_valid"></span>
                <span class="status_invalid"></span>
                <span class="status_ban"></span>
            </div>
            <div>
                <input class="form-check-input" type="checkbox" id="checkbox" v-model="isBan">
                <label class="form-check-label" style="color: #fff" for="checkbox">Danh sách BAN</label>
            </div>
            <Search/>
        </div>
        <div class="manager-main">
            <ListToken @popUp="popUp" />
             <Paginate/>
        </div>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
import { mapState } from 'vuex'
import Search from '../Search.vue'
import Paginate from './components/Paginate.vue'
import ManagerEdit from './components/ManagerEdit.vue'
import ListToken from './components/ManagerListTokens.vue'

export default {
    components: {
        Search,
        Paginate,
        ManagerEdit,
        ListToken
    },
    data() {
        return {
            isShow: false,
            indexToken: 0,
            isBan: false
        }
    },
    watch: {
        isBan() {
            if(this.isBan == true)
                this.$store.dispatch('getFilterTokens');
            else
                this.$store.dispatch('getTokens');
        }
    },
    mounted() {      
       this.checkLogin();
       this.getTokens()
    },
    computed:{ ...mapState(['token'])     
    },
    methods: {
       ...mapActions(['checkLogin', 'getTokens']),
       edit(index){
           this.index = index;
           this.isShow = true;
       },
       popUp(data){
           this.isShow =! this.isShow;
           this.indexToken = data;
       }
    },
}
</script>
<style lang="css" scoped>
.manager {
    position: absolute;
    top: 80px;
    left: 0;
    width: 95%;
    transform: translateX(2.5%);
}

.manager .form-control {
    border-radius: 20px;
    width: 100%;
    padding: 8px 20px;
}
.manager span {
    display: inline-block;
    width: 20px;
    height: 20px;
    margin:  4px 10px;
    border: 1px solid #000;
    border-radius: 5px;
}

.manager-status .status_valid {
    background: #fff;
}
.status_invalid {
    background: #FFC947;
}
.status_ban {
    background: rgb(219, 13, 13);
}

</style>