import axios from 'axios'
var url = 'http://reqapi.kmphimgiare.com/api';
const state = {
    username: '',
    password: '',
    time: '',
    qualify: 0,
}
const getters = {

}
const actions = {
    async getAccount({ commit }, payload) {
        try {
            // check localStorage
            const response = await axios.post(url + '/get-account', payload);
            if (response.data == 460) {
                commit('ALERT', ['Token tạm thời bị khóa', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 461) {
                commit('ALERT', ['Đã lấy quá nhiều, vui lòng quay lại hôm sau', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 462) {
                commit('ALERT', ['Token không chính xác', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 463) {
                commit('ALERT', ['Token đã hết hạn', 0]);
                commit('HAVING_ERROR');
                return;
            }
            commit('GET_ACC_CLIENT', response.data);
        } catch (err) {
            console.log('err login: ' + err)
        }
    },
    async getNewAccount({ commit }, payload) {
        try {
            // check localStorage
            const response = await axios.post(url + '/get-account-new', payload);

            if (response.data.error == 460) {
                commit('ALERT', ['Token tạm thời bị khóa', 0]);
                commit('HAVING_ERROR');
                commit('GET_ACC_CLIENT');
                return;
            }
            if (response.data.error == 464) {
                commit('ALERT', ['Token này đã bị vô hiệu hóa', 0]);
                commit('HAVING_ERROR');
                return;
            }
            if (response.data.error == 461) {
                commit('ALERT', ['Đã lấy quá nhiều, vui lòng quay lại hôm sau', 0]);
                commit('HAVING_ERROR');

                return;
            }
            if (response.data.error == 462) {
                commit('ALERT', ['Token không chính xác', 0]);
                commit('HAVING_ERROR');

                return;
            }
            if (response.data.error == 463) {
                commit('ALERT', ['Token đã hết hạn', 0]);
                commit('HAVING_ERROR');
                return;
            }


            commit('GET_ACC_CLIENT', response.data);
        } catch (err) {
            console.log('err login: ' + err)
        }
    },
}
const mutations = {
    GET_ACC_CLIENT(state, data) {
        state.username = data.username;
        state.password = data.password;
        state.time = data.time;
        state.qualify = data.qualify;
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}