// import axios from 'axios'
const state = {
    text: '',
    type: 0,
    isShow: false
}
const getters = {

}
const actions = {

}
const mutations = {
    ALERT(state, data) {
        state.text = data[0];
        state.type = data[1];
        state.isShow = true
        setTimeout(() => {
            state.text = '',
                state.type = 0,
                state.isShow = false
        }, 3500);
    }
}
export default {
    state,
    getters,
    actions,
    mutations
}