import { createRouter, createWebHistory } from 'vue-router'
// import components
import Home from '../components/client/Home.vue'
import GetAccount from '../components/client/GetAccount.vue'
import About from '../components/About.vue'
import NotFound from '../components/404.vue'
import Login from '../components/Login.vue'
import AddAccount from '../components/admin/AddAccount.vue'
import Manager from '../components/admin/Manager.vue'
import GetToken from '../components/admin/GetToken.vue'
import Quote from '../components/client/Quote.vue'
const routes = [
    { path: '/', component: Home },
    { path: '/about', component: About },
    { path: '/get-account', component: GetAccount },
    { path: '/login-admin-root', component: Login },
    { path: '/add-account', component: AddAccount },
    { path: '/manager', component: Manager },
    { path: '/get-token', component: GetToken },
    { path: '/price', component: Quote },
    { path: '/:pathMatch(.*)*', component: NotFound }

]
const router = createRouter({
    history: createWebHistory(),
    routes
})
export default router