<template lang="">
    <div class="get-token p-5">
        <h3>make token</h3>
        <form class="">
            <div class="my-1">
                <select v-model="data.time"  class="form-select">
                    <option selected :value=".5" >15 ngày</option>
                    <option :value="1" >1 tháng</option>
                    <option :value="3" >3 tháng</option>
                </select>
            </div>
            <div class="form-input mx-auto d-flex align-items-center py-2">
               <input  class="form-check-input" type="radio" id="one" value="sd" v-model="picked">
                <label class="form-check-label px-2" for="one">HD</label>
                
                <input class="form-check-input"  type="radio" id="two" value="4k" v-model="picked">
                <label class="form-check-label px-2" for="two">4K</label>
                <button @click.prevent="makeToken" class="btn btn-submit mx-auto py-2 px-4">Get Token</button>
            </div>
        </form>
            <div class="my-2">
                <input v-model="data.code" type="text" class="form-control" placeholder="Kết quả">
            </div>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
export default {
    data() {
        return {
            picked: 'sd',
            data: {
                code: '',
                time: .5,
                qualify: 0
            }
        }
    },
    mounted() {      
       this.checkLogin();
    },
    methods: {
       ...mapActions(['checkLogin']),
       makeToken() {

        if(this.picked === 'sd'){
            this.data.qualify = 0;
        }
         if(this.picked === '4k'){
            this.data.qualify = 1;
        }
        this.data.code = this.makeid(15);
        this.$store.dispatch('storeToken', this.data)
       },
       makeid(length) {
            let result           = '';
            let characters       = 'ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789';
            let charactersLength = characters.length;
            for ( let i = 0; i < length; i++ ) {
            result += characters.charAt(Math.floor(Math.random() * 
                charactersLength));
            }
        return result;
        }
    },
}
</script>
<style lang="css" scoped>
.get-token {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: rgba(255, 255, 255, 0.863);

    border-radius: 20px;
}
.get-token h3 {
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    color: #000000;
}
.get-token .form-control {
    border-radius: 5px;
    padding: 10px 20px;
}
.get-token form .form-control {
    width: 90%;
}
.get-token .btn-submit {
    background: #e01283;
    color: #fff;
    border-radius: 20px;
    padding: 0px 20px;
} 
@media only screen and (max-width: 600px) {

    .get-token {
        width: 90%;
    }
}
</style>