<template lang="">
    <div v-if="alert.isShow" class="alert d-flex align-items-center">
        <div v-if="alert.type == 1" class="icon success">
           <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-check2-circle" viewBox="0 0 16 16">
            <path d="M2.5 8a5.5 5.5 0 0 1 8.25-4.764.5.5 0 0 0 .5-.866A6.5 6.5 0 1 0 14.5 8a.5.5 0 0 0-1 0 5.5 5.5 0 1 1-11 0z"/>
            <path d="M15.354 3.354a.5.5 0 0 0-.708-.708L8 9.293 5.354 6.646a.5.5 0 1 0-.708.708l3 3a.5.5 0 0 0 .708 0l7-7z"/>
            </svg>
        </div>
        <div v-if="alert.type == 0" class="icon failure">
            <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-x-circle" viewBox="0 0 16 16">
            <path d="M8 15A7 7 0 1 1 8 1a7 7 0 0 1 0 14zm0 1A8 8 0 1 0 8 0a8 8 0 0 0 0 16z"/>
            <path d="M4.646 4.646a.5.5 0 0 1 .708 0L8 7.293l2.646-2.647a.5.5 0 0 1 .708.708L8.707 8l2.647 2.646a.5.5 0 0 1-.708.708L8 8.707l-2.646 2.647a.5.5 0 0 1-.708-.708L7.293 8 4.646 5.354a.5.5 0 0 1 0-.708z"/>
            </svg>
        </div>
        <div class="description " :class="[{success: alert.type == 1} , {failure: alert.type == 0}]">
            <p>
                {{ alert.text }}
            </p>
        </div>
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
    computed: 
        mapState(['alert']),
}
</script>
<style lang="css" scoped>
.alert {
    position: fixed;
    top: 100px;
    right: 0;
    z-index: 999;
    width: 350px;
    padding: 20px;

    background: #fff;
    border-radius: 15px 0 0 15px;
    border: 2px solid #0A1931;
    box-shadow: 3px 3px #0A1931;

    animation-name: alertBlur;
    animation-duration: 1s;
}
@keyframes alertBlur {
  0% {
      opacity: 0.6;
      right: -200px;
  }
  100% {
      
      right: 0;
      opacity: 1;
  }
}
.alert .icon svg {
    width: 40px;
    height: 40px;
    margin: 0 10px;
}
.alert .icon.success svg {
    color: rgb(0, 175, 15);
}
.description p {
    padding: 0 10px;
    margin: 0;
    font-size: 18px;
}
.description.success p {
    color: rgb(0, 175, 15);
}
.alert .icon.failure svg {
    color: rgb(226, 8, 0);
}
.description.failure p {
    color: rgb(226, 8, 0);
}
</style>