import axios from 'axios'
import router from '../../router'
var url = 'http://reqapi.kmphimgiare.com/api';
const state = {

}
const getters = {

}
const actions = {
    async search({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.get(url + '/search-token?searching=' + payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('GET_TOKENS', response.data.data);
            commit('GET_PAGE_MANAGER', [response.data.current_page, response.data.last_page]);
            console.log(response);
        } catch (error) {
            // router.push({path: 'login'});
            console.log('oke co loi ne: ' + error);
        }
    },
}
const mutations = {

}
export default {
    state,
    getters,
    actions,
    mutations
}