import router from '../../router'
import axios from 'axios'
var url = 'http://reqapi.kmphimgiare.com/api';
const state = {
    username: '',
}
const getters = {

}
const actions = {
    async loginUser({ commit }, payload) {
        try {
            const response = await axios.post(url + '/api/login', payload);
            window.localStorage.setItem('token', response.data.token);
            commit('');
            router.push({ path: '/manager' });
        } catch (err) {
            router.push({ path: '/login-admin-root' });
            console.log('err login: ' + err)
        }
    },
    async checkLogin({ commit }) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.get(url + '/user', { headers: { Authorization: 'Bearer ' + token } });
            commit('SET_USER', response.data);
        } catch (error) {
            router.push({ path: '/login-admin-root' });
        }
    },
}
const mutations = {
    SET_USER(state, data) {
        state.username = data.username;
    },
    REMOVE_USER(state) {
        state.username = '';
    }

}
export default {
    state,
    getters,
    actions,
    mutations
}