 .form-input .form-controls{
    border-radius: 20px;
    padding: 8px 20px;
      border:none !important;
      outline: none !important;
 }
      .form-input svg {
    display: inline-block;
    height: 30px;
    margin: auto;
    color:#246AF2;
}
</style>