<template lang="">
    <div class="account">
       <AddAcc />
        <div class="list-account mx-auto px-4 mt-3">
            <div class="p-2 d-flex">
                <div>
                    <input class="form-check-input" type="checkbox" id="checkbox" v-model="isBan">
                    <label class="form-check-label" for="checkbox">Account còn lại</label>
                </div>
                <div>
                     <span class="px-3"> Tổng: {{account.totalAcc}} </span>
                </div>
            </div>
            <table v-if="account.list.length > 0" class="table table-hover mt-1 w-100 ">
                <thead>
                    <tr>
                        <th scope="col" class="title">Email</th>
                        <th scope="col" class="title">Password</th>
                        <th scope="col" class="title">Qualify</th>
                        <th scope="col" class="title">Status</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(account, index) in account.list" :key="index">
                        <td>
                            {{ account.username }}
                        </td>
                        <td>
                            {{ account.password }}
                        </td>
                        <td v-if="account.qualify == 0"> 
                            <p class="standard">Standard</p>
                        </td>
                         <td v-if="account.qualify == 1"> 
                            <p class="premium">Premium</p>
                        </td>
                        <td v-if="account.type == 1"> 
                            <p class="used">Đã dùng</p>
                        </td>
                        <td v-if="account.type == 0"> 
                            <p class="use">Chưa dùng</p>
                        </td>
                    </tr>
                  
                </tbody>
            </table>
                <div class="account-pagination">
                    <nav aria-label="Page navigation example">
                    <ul class="pagination">
                        <li @click="changePage(account.curPage -1)" class="page-item">
                            <a class="page-link" href="#">
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-caret-left-fill" viewBox="0 0 16 16">
                                <path d="m3.86 8.753 5.482 4.796c.646.566 1.658.106 1.658-.753V3.204a1 1 0 0 0-1.659-.753l-5.48 4.796a1 1 0 0 0 0 1.506z"/>
                                </svg>
                            </a>
                        </li>
                        <li @click="changePage(index + 1)" v-for="(page, index) in account.lastPage" :key="index" 
                        class="page-item" >
                            <a class="page-link" :class="{active: account.curPage == index +1}" href="#">{{index + 1}}</a>
                        </li>
                 
                        <li @click="changePage(account.curPage +1)" class="page-item">
                            <a class="page-link" href="#">
                                <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-caret-right-fill" viewBox="0 0 16 16">
                                <path d="m12.14 8.753-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"/>
                                </svg>
                            </a>
                        </li>
                    </ul>
                    </nav>
                </div>
        </div>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
import { mapState } from 'vuex'
import AddAcc from './components/AddAcc.vue'
export default {
    components: {
        AddAcc
    },
    data() {
        return {
           
            isBan: false
        }
    },
    watch: {
        isBan() {
            if(this.isBan == true)
                this.$store.dispatch('getFilterAccounts')
            else
                this.$store.dispatch('getAccounts');
        }
    },
    computed:{ ...mapState(['account'])},
    mounted() {      
       this.checkLogin();
       this.getAccounts();
    },
    methods: {
       ...mapActions(['checkLogin' , 'getAccounts']),
      
       changePage(index){
           if(index <= this.account.lastPage){
                if(this.isBan == true)
                    this.$store.dispatch('getFilterAccounts', index);
                else
                    this.$store.dispatch('getAccounts', index);

           }
          
       }
    },
}
</script>
<style lang="css" scoped>


.list-account {
    position: relative;
    border-radius: 20px;
    width: 80%;
    background: #ECECEC;
}
.list-account .title {
    color: #0078E7;
    border-bottom: 2px solid #0078E7;

}
.account-pagination nav{
    margin: 0 auto;
    display: table;
}
.account-pagination .page-link {
    background: transparent;
    border: none;
    border-radius: 0px !important;
    color: #000;
     outline: none !important;
}
.account-pagination .page-link:focus{
    outline: 0 !important;
    box-shadow: none !important;
}
.account-pagination .page-link.active {
    border-bottom: 2px solid #0078E7;
}
p {
    padding: 0;
    margin: 0;
}
.standard {
    color: #22bb33;
}
.premium {
    color: #185ADB;
}
.used {
    color: #DD067B;
}
.use {
    color: #000;
}
</style>