<template lang="">
    <div>
        <form class="d-flex align-items-center justify-content-between mx-auto  w-75">
            <div class="form-input ">
                <input v-model="acc.username" type="email" class="form-control" placeholder="Email">
            </div>
            <div class="form-input mx-3">
                <input v-model="acc.password" type="text" class="form-control" placeholder="Password">
            </div>
            <div class="form-input w-25 mx-auto">
               <input  class="form-check-input" type="radio" id="one" value="sd" v-model="picked">
                <label class="form-check-label px-2" for="one">HD</label>
                <br>
                <input class="form-check-input"  type="radio" id="two" value="4k" v-model="picked">
                <label class="form-check-label px-2" for="two">4K</label>
            </div>
            <button @click.prevent="store" type="submit" class="btn btn-add-account">Add Account</button>
        </form>
    </div>
</template>
<script>
export default {
    data() {
        return {
            picked: 'sd',
            acc: {
                username: '',
                password: '',
                qualify: 0
            },
        }
    },
    methods: {
         store(){
            if(this.picked === 'sd'){
                this.acc.qualify = 0 //
            }
            if(this.picked === '4k'){
                this.acc.qualify = 1 //
            } 
           this.$store.dispatch('storeAccount', this.acc);
       },
    },
}
</script>
<style lang="css" scoped>
    .account .form-control {
    border-radius: 20px;
    width: 100%;
    padding: 5px 20px;
}
.account .form-input {
    width: 35%;
}
.account .btn-add-account {
    background: #DD067B;
    color: #fff;
    border-radius: 20px;
    padding: 0 20px;
}
.form-check-input:checked {
     background-color: #DD067B;
}
.form-check-label {
    color: #fff;
}
</style>