import axios from 'axios'
import router from '../../router'
var url = 'http://reqapi.kmphimgiare.com/api';
const state = {

}
const getters = {

}
const actions = {
    async getFilterTokens({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.get(url + '/filter-token?page=' + payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('GET_TOKENS', response.data.data);
            commit('GET_PAGE_MANAGER', [response.data.current_page, response.data.last_page]);
            console.log(response.data);
        } catch (err) {

            console.log('err login: ' + err)
        }
    },
    async getFilterAccounts({ commit }, payload) {
        try {
            let token = window.localStorage.getItem('token');
            if (token === null)
                router.push({ path: 'login-admin-root' });
            const response = await axios.get(url + '/filter-account?page= ' + payload, { headers: { Authorization: 'Bearer ' + token } });
            commit('SET_ACCOUNT', response.data.data);
            commit('SET_PAGE', [response.data.current_page, response.data.last_page, response.data.total]);

        } catch (err) {

            console.log('err login: ' + err)
        }
    },
}
const mutations = {

}
export default {
    state,
    getters,
    actions,
    mutations
}