<template lang="">
    <div class="login p-5">
        <h3>sign in</h3>
        <form>
            <div class="mb-3">
                <input v-model="user.username" type="text" class="form-control" id="exampleInputEmail1" placeholder="Username">
            </div>
            <div class="mb-3">
                <input v-model="user.password" type="password" class="form-control" id="exampleInputPassword1" placeholder="Password">
            </div>
            <button @click.prevent="login" type="submit" class="btn btn-submit w-100">Submit</button>
        </form>
    </div>
</template>
<script>
export default {
    data() {
        return {
            user: {
                username: '',
                password: ''
            }
        }
    },
    methods: {
        login() {
            this.$store.dispatch('loginUser', this.user);
        }
    },
}
</script>
<style lang="css" scoped>
.login {
    background: #ECECEC;
    border-radius: 20px;

    position: absolute;
    top: 50%;
    left: 50%;
    width: 30%;
    transform: translate(-50%, -50%);
}
.login h3 {
    padding-bottom: 20px;
    text-align: center;
    text-transform: uppercase;
    font-weight: bold;
    color: #0078E7;
}
.login .form-control {
    border-radius: 30px;
    padding: 12px 20px;
}
.login .btn-submit {
    border-radius: 20px;
    background: #0078E7;
    color: #fff;
    padding: 9px 0;
}
</style>