<template lang="">
    <div class="main-list-token">
        <div class="main-lists p-3">
            <div v-for="(token, index) in token.list" :key="index" class="main-lists__item pb-3 row"
                :class="{ banItem: token.status == 1}">
                <div class="col-2">
                    {{ token.code }}
                </div>
                <div class="col-1">
                    <p v-if="token.time == .5">
                        15 ngày
                    </p>
                     <p v-else>
                         {{ token.time }} tháng
                    </p>
                  
                </div>
                 <div class="col-1">
                   <p class="standard" v-if="token.qualify == 0">Standard</p>
                   <p  class="premium" v-if="token.qualify == 1">Premium</p>
                </div>
                <div class="col-5">
                    <div v-for="(account, index) in token.accounts " :key="index" class="row">
                        <div class="col-5">
                            {{index+1}} {{ account.username }}               
                        </div>
                        <div class="col-4">
                            {{ account.password }}
                        </div>
                        <div class="col-3">
                            {{ formatTime(account.pivot.updated_at) }}
                        </div>
                        
                    </div>
                </div>
                <div class="col-1">
                    <span class="status-item" 
                    :class="[
                    {medium: token.accounts.length >= 6},
                    {hard: token.accounts.length >= 9}]">

                    </span>
                </div>
                <div class="col-2 function">
                    <svg @click="change(index)" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-pencil-square" viewBox="0 0 16 16">
                    <path d="M15.502 1.94a.5.5 0 0 1 0 .706L14.459 3.69l-2-2L13.502.646a.5.5 0 0 1 .707 0l1.293 1.293zm-1.75 2.456-2-2L4.939 9.21a.5.5 0 0 0-.121.196l-.805 2.414a.25.25 0 0 0 .316.316l2.414-.805a.5.5 0 0 0 .196-.12l6.813-6.814z"/>
                    <path fill-rule="evenodd" d="M1 13.5A1.5 1.5 0 0 0 2.5 15h11a1.5 1.5 0 0 0 1.5-1.5v-6a.5.5 0 0 0-1 0v6a.5.5 0 0 1-.5.5h-11a.5.5 0 0 1-.5-.5v-11a.5.5 0 0 1 .5-.5H9a.5.5 0 0 0 0-1H2.5A1.5 1.5 0 0 0 1 2.5v11z"/>
                    </svg>
                    <svg @click="banTokeni(token.id)" xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-person-x" viewBox="0 0 16 16">
                    <path d="M6 8a3 3 0 1 0 0-6 3 3 0 0 0 0 6zm2-3a2 2 0 1 1-4 0 2 2 0 0 1 4 0zm4 8c0 1-1 1-1 1H1s-1 0-1-1 1-4 6-4 6 3 6 4zm-1-.004c-.001-.246-.154-.986-.832-1.664C9.516 10.68 8.289 10 6 10c-2.29 0-3.516.68-4.168 1.332-.678.678-.83 1.418-.832 1.664h10z"/>
                    <path fill-rule="evenodd" d="M12.146 5.146a.5.5 0 0 1 .708 0L14 6.293l1.146-1.147a.5.5 0 0 1 .708.708L14.707 7l1.147 1.146a.5.5 0 0 1-.708.708L14 7.707l-1.146 1.147a.5.5 0 0 1-.708-.708L13.293 7l-1.147-1.146a.5.5 0 0 1 0-.708z"/>
                    </svg>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapActions } from 'vuex'
import { mapState } from 'vuex'
export default {
    data() {
        return {
            isBan: false
        }
    },
 
    watch: {
        isBan() {
            if(this.isBan == true)
                this.$store.dispatch('getFilterTokens');
            else
                this.$store.dispatch('getTokens');
        }
    },
     computed:{ ...mapState(['token'])     
    },
    methods: {
       ...mapActions(['checkLogin', 'getTokens']),
       banTokeni(id){
          const myPromie = new Promise((resolve) => {
              this.$store.dispatch('banToken', id);
              resolve('sucess');
          })
            myPromie.then(()=>{
                this.$store.dispatch('getTokens')
            })
       },
       
       formatTime(time){
            let date = time.slice(8,10);
            let month = time.slice(6,7);
            let year = time.slice(2,4);
            let hour = time.slice(11,13);
            let minute = time.slice(14,16);
            let timing = date + '/' + month + '/' + year + ' ' 
                        + (parseInt(hour) + 7 )+ ':' + minute;
            return timing ;
       },
       change(index){
           
           this.$emit('popUp', index);
       }
    }
}
</script>
<style lang="css" scoped>
.main-list-token {
    background: rgb(247, 247, 247);
    border-radius: 20px;
    overflow: hidden;
}
.main-lists__item {

    transition: ease all .2s;
}
.main-lists__item:hover {
    background: rgba(204, 204, 204, 0.466);
}
.main-lists {
    border-radius: 20px;
}
.function svg {
    padding: 0 5px;
    height: 30px;
    width: 30px;
    border-radius: 20px;
    cursor: pointer;
}
.function svg:nth-child(1) {
    color:#22bb33;
}
.function svg:nth-child(2) {
    color:#f44336;
}
.status_ban {
    background: rgb(219, 13, 13);
}
.table .ban {
    border-left: 4px solid red;
}
.banItem .function svg:nth-child(2) {
    color:#fff;
    background-color: rgb(219, 13, 13);
} 
.banItem {
    border-left: 4px solid red;
    background: #fae4e4;
}
.status-item {
    display: inline-block;
    width: 20px;
    height: 20px;

    border: 1px solid #000;
    border-radius: 5px;
}

.medium {
     background-color: #FFC947;
}
.hard {
     background-color: #f44336;
}
.standard {
    color: #22bb33;
}
.premium {
    color: #185ADB;
}
</style>