<template lang="">
    <div class="account-pagination ">
        <nav class="navPagination" aria-label="Page navigation  example">
            <ul class="pagination ">
                <li @click="changePage(token.curPage -1)" class="page-item">
                    <a class="page-link" href="#">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-caret-left-fill" viewBox="0 0 16 16">
                        <path d="m3.86 8.753 5.482 4.796c.646.566 1.658.106 1.658-.753V3.204a1 1 0 0 0-1.659-.753l-5.48 4.796a1 1 0 0 0 0 1.506z"/>
                        </svg>
                    </a>
                </li>
                <li @click="changePage(index + 1)" v-for="(page, index) in token.lastPage" :key="index" 
                class="page-item" >
                    <a class="page-link" :class="{active: token.curPage == index +1}" href="#">{{index + 1}}</a>
                </li>
            
                <li @click="changePage(token.curPage +1)" class="page-item">
                    <a class="page-link" href="#">
                        <svg xmlns="http://www.w3.org/2000/svg" width="16" height="16" fill="currentColor" class="bi bi-caret-right-fill" viewBox="0 0 16 16">
                        <path d="m12.14 8.753-5.482 4.796c-.646.566-1.658.106-1.658-.753V3.204a1 1 0 0 1 1.659-.753l5.48 4.796a1 1 0 0 1 0 1.506z"/>
                        </svg>
                    </a>
                </li>
            </ul>
        </nav>
    </div>
</template>
<script>
import { mapState } from 'vuex'
export default {
    data() {
        return {
            index: 0
        }
    },
    computed:{ ...mapState(['token'])     
    },
    methods: {
        changePage(index){
               if(index <= this.token.lastPage){
                   if(this.isBan == true)
                        this.$store.dispatch('getFilterTokens', index);
                    else
                        this.$store.dispatch('getTokens', index);
    
               }
           },
    },
}
</script>
<style lang="css" scoped>
/* .account-pagination {
    
} */
.account-pagination .page-link {
    background: transparent;
    border: none;
    border-radius: 0px !important;
    color: #fff;
     outline: none !important;
}
.account-pagination .page-link:focus{
    outline: 0 !important;
    box-shadow: none !important;
}
.account-pagination .page-link.active {
    border-bottom: 2px solid #0078E7;
}
.navPagination{
    margin: 0 auto;
    display: table!important;
}
</style>